.banner {
  margin-top: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: none;
  min-height: 600px;
  background-color: #212121;
  background-size: cover;
  background-image: url(./images/banner-background.png);

  @media screen and (min-width: 1366px) {
    background-position: 50% !important;
  }

  @media screen and (max-width: 1200px) and (min-width: 320px) {
    max-height: none;
    min-height: 0;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  @media screen and (max-width: 992px) and (min-width: 320px) {
    max-height: none;
    min-height: 0;
  }

  .content-wrapper {
    margin: auto;
    max-width: 1380px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    color: #595959;

    .logo-image {
      max-width: 455px;
      margin: 20px;

      @media screen and (max-width: 1200px) and (min-width: 320px) {
        width: 100%;
        max-width: 40%;
      }

      @media screen and (max-width: 1120px) and (min-width: 320px) {
        max-width: 40%;
      }

      @media screen and (max-width: 992px) and (min-width: 320px) {
        text-align: center;
      }

      @media screen and (max-width: 780px) and (min-width: 320px) {
        max-width: 80%;
        width: 100%;
        margin: 35px auto;
      }

      @media screen and (max-width: 570px) and (min-width: 320px) {
        max-width: 100%;
        width: 100%;
        margin: 35px auto;
      }

      .responsive-img {
        width: 100%;

        @media screen and (max-width: 992px) and (min-width: 320px) {
          max-width: 100%;
          width: 280px;
          margin: auto;
        }
      }
    }

    .logo-text-wrapper {
      color: #fff;
      width: 600px;
      margin-top: 30px;

      @media screen and (max-width: 1200px) and (min-width: 570px) {
        text-align: center;
      }

      @media only screen and (max-device-width: 1024px) and (min-device-width: 768px) and (orientation: landscape) {
        width: 480px;
      }

      @media screen and (max-width: 570px) and (min-width: 320px) {
        max-width: 95%;
        width: 95%;
        margin-top: 0;
      }

      h1,
      h2,
      h3,
      h4,
      h5,
      h6 {
        margin: 0;
        padding: 6.5px 0;
      }

      h1 {
        font-size: 48px;
        letter-spacing: normal;

        @media screen and (max-width: 992px) and (min-width: 320px) {
          font-size: 35px;
        }

        @media screen and (max-width: 992px) and (min-width: 320px) and (min-width: 580px) {
          width: 100% !important;
        }
      }

      h3 {
        line-height: 1.3;
        font-size: 20px;

        @media screen and (max-width: 992px) and (min-width: 320px) {
          font-size: 20px;
        }
      }

      a {
        margin-top: 30px;
        margin-bottom: 30px;
      }

      .get-started {
        color: #fff;
        border-color: #640a92;
        background-color: #640a92;
      }

      .btn {
        max-width: 375px;
        padding: 16px 58px;
        transition: all 0.4s ease;
        text-transform: uppercase;
        font-family: Helvetica;
        border: 2px solid transparent;
        border-radius: 5px;
        font-size: 14px;

        &:hover {
          border-color: #fff;
          color: #000;
          background-color: #fff;
        }

        @media screen and (max-width: 570px) and (min-width: 320px) {
          max-width: 100%;
          width: 100%;
        }
      }
    }
  }
}

.ready-made {
  .ready-made-section {
    margin-right: -15px;
    margin-left: -15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 75px;
    overflow: hidden;
    background-size: cover;

    @media screen and (max-width: 599px) {
      background-repeat: no-repeat !important;
      background-size: cover !important;
      justify-content: center;
      height: 230px;
      justify-content: center;
      padding-left: 0;
    }
  }

  .ready-made-section-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-shrink: 0;
    position: relative;
    z-index: 2;

    @media screen and (max-width: 599px) {
      width: 280px;
    }
  }

  .ready-made-section-title {
    color: #fff;
    text-align: center;
    margin-bottom: 5px;
    font-size: 30px;
    letter-spacing: -0.94px;

    @media (max-width: 599px) {
      text-align: center;
      width: 100%;
    }
  }

  .ready-made-section-sub {
    color: #fff;
    font-size: 16px;
    text-align: center;
    margin-bottom: 20px;

    @media screen and (max-width: 599px) {
      width: 100%;
    }
  }

  .btn-purple {
    color: #fff;
    border-color: #640a92;
    background-color: #640a92;

    @media (max-width: 599px) {
      width: 100%;
    }
  }

  .btn {
    padding: 16px 58px;
    transition: all 0.4s ease;
    text-transform: uppercase;
    font-family: Helvetica;
    border: 2px solid transparent;
    border-radius: 5px;
    font-size: 14px;

    &:hover {
      color: #000;
      border-color: #fff;
      background-color: #fff;
    }
  }

  .ready-made-section-image {
    position: relative;

    @media (max-width: 599px) {
      display: none;
    }
  }

  .ready-made-section-image img {
    max-width: 500px;
    max-height: 280px;
  }

  .top-left {
    background-image: url(./images/ready-made-top-left.jpg);

    @media screen and (max-width: 599px) {
      background-image: url(./images/ready-made-top-left-logo-bg.png);
    }

    .ready-made-section-image {
      right: -75px;

      @media (max-width: 1200px) and (min-width: 991px) {
        right: 35px;
      }
    }
  }

  .top-right {
    background-image: url(./images/ready-made-top-right.png);

    @media screen and (max-width: 599px) {
      background-image: url(./images/ready-made-top-right-logo-bg.png);
    }

    .ready-made-section-image {
      right: 0px;

      @media (max-width: 1200px) and (min-width: 991px) {
        right: 10px;
      }
    }
  }

  .bottom-left {
    background-image: url(./images/ready-made-bottom-left.jpg);

    @media screen and (max-width: 599px) {
      background-image: url(./images/ready-made-bottom-left-logo-bg.png);
    }

    @media (max-width: 1200px) and (min-width: 991px) {
      .ready-made-section-image[data-v-5f6fe0a0] {
        right: 15px;
      }
    }
  }

  .bottom-right {
    background-image: url(./images/ready-made-bottom-right.jpg);

    @media screen and (max-width: 599px) {
      background-image: url(./images/ready-made-bottom-right-logo-bg.png);
    }

    @media (max-width: 1200px) and (min-width: 991px) {
      .ready-made-section-image {
        right: 80px;
      }
    }
  }
}
